@import url(https://fonts.googleapis.com/css?family=Muli:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Muli', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2B2C28;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: auto;
  align-items: center;
  text-align: center;
  background-color: rgba(51, 153, 137, 0.35);
  height: 80px;
}

@media screen and (max-width: 900px) {
  nav {
    display: flex;
    flex-direction: column;
    height: 310px;
  }
  .navLinks {
    width: 100vw;
  }
}

.one {
  grid-column: 1;
}

.two {
  grid-column: 2;
}

.three {
  grid-column: 3;
}

.four {
  grid-column: 4;
}

.navLinks {
  font-size: 1.5em;
  text-transform: uppercase;
  text-decoration: none;
  color: #2B2C28;
  height: 100%;
  padding: 1em 1em;
  cursor: pointer;
}

.navLinks:hover {
  color: #FFFAFB;
  background-color: rgba(51, 153, 137, 0.75);
}

.active {
  color: #FFFAFB;
  background-color: rgba(51, 153, 137, 0.25);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 25vh;
}

h1, h2 {
  text-align: center;
  color: #FFFAFB;
}

h1 {
  font-size: 6em;
  margin: 25px;
  text-shadow: 0 1px 0 #ccc,
               0 2px 0 #c9c9c9,
               0 3px 0 #bbb,
               0 4px 0 #b9b9b9,
               0 5px 0 #aaa,
               0 6px 1px rgba(0,0,0,.1),
               0 0 5px rgba(0,0,0,.1),
               0 1px 3px rgba(0,0,0,.3),
               0 3px 5px rgba(0,0,0,.2),
               0 5px 10px rgba(0,0,0,.25),
               0 10px 10px rgba(0,0,0,.2),
               0 20px 20px rgba(0,0,0,.15);
}

h2 {
  font-size: 3em;
  margin-top: 0;
  margin-bottom: 50px;
}

h2 span {
  color: #339989;
  font-weight: bold;
}

.inputField {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 75vw;
  margin: 15px;
}

input {
  border: none;
  width: 75%;
  height: 40px;
  margin: 20px;
  padding: 10px;
  font-size: 2.25em;
  text-align: center;
  color: #2B2C28;
  background-color: rgba(50, 50, 50, 0.25);
  border-bottom: 15px solid rgba(25,25,25,0.25);
}

input:focus {
  outline: none;
  background-color: rgba(100, 100, 100, 0.25);
  border-bottom: 15px solid rgba(25,25,25,0.35);
}

input:hover {
  background-color: rgba(75, 75, 75, 0.25);
}

.inputFieldLabel {
  color: #2B2C28;
  font-size: 3em;
}

@media screen and (max-width: 1100px) {
  input {
    width: 85vw; 
  }
}

@media screen and (max-width: 900px) {
  input {
    width: 95vw
  }
}

.colorInput {
  width: 350px;
  font-size: 1em;
  margin: 100px 20px;
  background-color: rgba(50, 50, 50, 0.1);
  border-bottom: 15px solid rgba(25,25,25,0.1);
}

.dark {
  color: #2B2C28;
}

.light {
  color: #FFFAFB;
}

::-webkit-input-placeholder {
  color: #FFFAFB;
  opacity: 0.65; 
}

::-ms-input-placeholder {
  color: #FFFAFB;
  opacity: 0.65; 
}

::placeholder {
  color: #FFFAFB;
  opacity: 0.65; 
}

.colors {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2.5em;
}

.listItem {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100px;
  font-size: 1.75em;
  background-color: rgba(25,25,25,0.25);
  margin-bottom: 25px;
}

@media screen and (max-width: 900px) {
  .listItem {
    font-size: 1.25em;
  }
}

@media screen and (max-width: 500px) {
  .listItem {
    font-size: 1em;
  }
}

.unicode {
  font-size: 2em;
  color: #2B2C28;
  text-align: center;
}

.unicode h3 {
  font-size: 1.5em;
  margin-bottom: 0;
}

.unicode p:hover {
  text-decoration: underline;
  color: #FFFAFB;
  cursor: pointer;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #339989;
  min-height: 69vh;
}

